<template>
  <el-card class="card">
    <img :src="userImg" class="image" />
    <div style="padding: 14px;">
      <div class="userinfo">个人信息</div>
      <div class="userinfo1">
        <span class="span1">用户ID:</span>
        <span class="span1">{{userId}}</span>
      </div>
      <div class="userinfo1">
        <span class="span1">用户名:</span>
        <span class="span1">{{userName}}</span>
      </div>
      <el-button type="warning" class="button" @click="changepasswordonclick()">修改密码</el-button>
      <el-dialog title="修改密码" :visible.sync="dialogVisible" width="30%">
        <el-form :rules="rules" ref="cgp" :model="form" label-width="125px">
          <el-form-item label="请输入旧密码" prop="oldpassword">
            <el-input type="password" v-model="form.oldpassword"></el-input>
          </el-form-item>
          <el-form-item label="请输入新密码" prop="newpassword">
            <el-input type="password" v-model="form.newpassword"></el-input>
          </el-form-item>
          <el-form-item label="再次输入新密码" prop="anpassword">
            <el-input type="password" v-model="form.anpassword"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="exit()">取 消</el-button>
          <el-button :plain="true" type="primary" @click="postform()">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </el-card>
</template>

<script>
import axios from "../../utils/axios";

export default {
  name: "Shuaiye",
  data() {
    var checkpass = (rules, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.newpassword) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      userId: 1,
      userName: "",
      userpassword: "",
      userImg: "",
      dialogVisible: false,
      form: {
        oldpassword: "",
        newpassword: "",
        anpassword: ""
      },
      rules: {
        oldpassword: [
          { required: true, message: "请输入旧密码", trigger: "blur" }
        ],
        newpassword: [
          { required: true, message: "请输入新密码", trigger: "blur" }
        ],
        anpassword: [
          {
            validator: checkpass,
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    changepasswordonclick() {
      this.form.oldpassword = "";
      this.form.newpassword = "";
      this.form.anpassword = "";
      this.dialogVisible = true;
    },
    postform() {
      var updpass = {
        uid: this.userID,
        password: this.form.oldpassword,
        newpassword: this.form.newpassword
      };
      this.$refs["cgp"].validate(valid => {
        if (valid) {
          axios.changepassword(updpass).then(Response => {
            console.log(Response);
            if (Response.success == 1) {
              this.dialogVisible = false;
              this.$message({
                showClose: true,
                message: "修改成功",
                type: "success"
              });
            } else if (Response.wrongpassword == 1) {
              this.$message({
                showClose: true,
                message: "原密码错误",
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: "未知错误",
                type: "success"
              });
            }
          });
        }
      });
    },

    exit() {
      console.log("爷进来啦");
      this.dialogVisible = false;
      this.$message({
        showClose: true,
        message: "已取消"
      });
    }
  },
  mounted() {
    this.userId = this.$store.state.userId;
    this.userName = this.$store.state.userName;
    this.userImg = this.$store.state.userImg;
    this.isAdmin = this.$store.state.isAdmin;
  }
};
</script>

<style scoped>
.card {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.userinfo {
  font-size: 40px;
  text-align: center;
}

.userinfo1 {
  position: relative;
  left: 26%;
  margin-top: 2%;
  margin-bottom: 2%;
}

.span1 {
  font-size: 24px;
  margin-left: 10%;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  width: 80px;
  height: 40px;
  padding: 0;
  float: right;
  margin-bottom: 2%;
}

.image {
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>